.form_group {
  margin-top: 29px;
}

.line {
  display: flex;
  gap: 7px;

  &+.line {
    margin-top: 32px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  text-align: left;
  flex: 1;
  gap: 9px;
}

.label {
  font-size: 1rem;
  line-height: 22px;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
}

.input {
  flex: 1;
  padding: 16px 19px;
  font-size: 1rem;
  line-height: 19px;
  background: #DFDFDF;
  border-radius: 6px;
}

.submit {
  width: 100%;
  height: 65px;
  margin-top: 34px;
  font-size: 1.313rem;
  font-weight: 700;
  color: var(--white);
  background: var(--primary2);
  border: 0;
  border-radius: 12px;
}